.invoice{
    width: 100%;
    min-height: 90vh;
    margin-bottom: 1rem;
    margin-top: 1rem;
    padding: 1.5rem 1rem;
    border-radius: .2rem;
    -webkit-box-shadow: 0px 2px 4px 0px #000000; 
    box-shadow: 0px 2px 4px 0px #000000;
    position: absolute;
    top: 0;
    left: 50%;
    background: white;
    transform: translate(-50%,0%);

    &__tittle{
        display: flex;
        justify-content: space-between;
        align-items: center;

    }
    &__heading{
        font-size: 2rem;
        font-weight: 300;
    }
    &__printButton{
        font-size: .865rem;
        padding: 0.5rem .8rem;
        border-radius: .2rem;
        background:  #d3d6d6;
    }
    &__time{
        font-size: .8165rem;
    }
    &__description{
        position: relative;
        border: 1px solid #ece8e8;
    }
    &__info{
        width: 100%;
        height: 100%;
        display: grid;
        padding-top: 2rem;
        padding-left: 2rem;
        padding-right: 2rem;
        margin-bottom: .5rem;
        grid-template-columns: repeat(2,1fr);
        
        p{
            font-size: .9rem;
        }
        @include breakpoint-down(small){
            grid-template-columns: repeat(1,1fr);
            .invoice__description__grid2{
                margin: 3rem auto;
            }
            .invoice__description__grid{
                margin: 2rem auto;
            }
        }
    }
    &__before{
        width: 4.375rem;
        height: 4.375rem;
        position: absolute;
        top: -.3%;
        right: -.2%;
        background-color: white; 
        .inner{
            width: 100%;
            height: 100%;
            background-color: #d3d6d6;
            clip-path: polygon(0 0, 0% 100%, 100% 100%);
        }
    }
    &__bar{
        width: 95%;
        margin: 1rem auto;
       
        height: 2px;
        background: #ece8e8;
    }
    &__description__grid2{
        margin-left: 60%;
        h2{
            font-size: 1.875rem;
            font-weight: 700;
            color: #9da3a6;
            margin-bottom: 1.5rem;
        }
        @include breakpoint-up(medium){
            margin-left: 40%;
        }
    }
    &__paid{
        color: white;
        font-size: 1rem;
        font-weight: 700;
        width: 100%;
        background: #1b9e1b;
        padding: .2rem;

    }
    &__company__logo{
        display: flex;
        margin-bottom: 3rem;
        img{
            width: 4rem;
            height: 4rem;
        }
        h2{
            margin-top: 1rem;
            font-size: 1rem;
            font-weight: 400;
            color: rgb(45, 106, 146);
        }
    }
    &__amount{
        width: 100%;
        padding-left: 2rem;
        padding-right: 2rem;
        padding-top: .5rem;

        border: 1px solid #adabab;
        border-radius: .2rem;
        text-align: center;
        margin-bottom: .5rem;
        p{
            margin: 0;
        }
        h2{
            color: black;
            font-size: 1.5rem;
            font-weight: 400;
            margin-top: 0;
            margin-bottom: 0;
        }
    }
    &__billinfo{
        padding-left: 2rem;
        padding-right: 2rem;
        margin-bottom: .5rem;
        font-size: 1rem;
        font-weight: 300;
        h2{
            margin: 0;
            font-size: 1.2rem;
            font-weight: 400;
        }
    }
    &__table1{
        margin-bottom: .5rem;
        table{
            width: 100%;
            border-collapse: collapse;
            thead{
                border: 1px solid #ece8e8;
                background: #e8ebeb;
                tr{
                    th{
                        padding-left: 1rem;
                        padding-right: .5rem;
                    }
                    .th1{
                        text-align: left;
                    }
                }
            }
            tbody{
                td{
                    padding-left: 1rem;
                    padding-right: 1rem;
                    padding-top: 1rem;
                }
                .td1{
                    text-align: center;
                }
            }
        }
        @include breakpoint-down(small){
            table thead {
                display: none;
            }

            table td {
                display: block;
            }
            table td::before {
                content: attr(data-heading) ": ";
                font-weight: bold;
            }
        }

    }
    &__table1__bar{
        width: 100%;
        height: 1px;
        background: #e8ebeb;
    }
    &__table2{
        display: flex;
        width: 50%;
        margin-left: auto;
        @include breakpoint-down(small){
            width: 100%;
            margin-left: 0%;
        }
    }
    &__table2__grid{
        width: 100%;
        margin-bottom: 1rem;
    }
    &__table2__item{
        width: 100%;
        padding: .1rem;
        font-size: .9rem;
        display: grid;
        grid-template-columns: repeat(2,1fr);
        border: 1px solid #ece8e8;
        text-align: right;
        .table2__text{
            width: 100%;
            padding-right: 1rem !important;
            border-right: 1px solid #ece8e8;
        }
        .table2__value{
            width: 100%;
            padding-right: 1rem !important;
        }
        .textbold{
            font-weight: 600;
            background: #e2e0e0;
        }
    }
    &__textnotes{
        h2{
            margin-top: .5rem;
            font-size: 1.2rem;
            font-weight: 300;
        }
    }
    &__paypallogo{
        display: flex;
        position: absolute;
        bottom: 0;
        right: 2%;
        text-align: center;
        font-size: .7rem;
        font-style: italic;
        img{
            margin-top: .8rem;
            margin-left: .5rem;
            width: 6.25rem;
            height: 1.25rem;
        }
    }

}
