@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;200;300;400;500;700&display=swap');

html{
  font-size: 100%;
  box-sizing: border-box;
  position: relative;
}
*,*::after,*::before{
  box-sizing: inherit;
}

body{
  margin: 0;
  padding: 0;
  width: 100%;
  height: 500vh;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: $font-med;
  color: black;
  line-height: 1.3;
  overflow-x: hidden;
  background-color: #F3F3F3;
  
}

ul,li{
  padding: 0;
  margin: 0;
}
h2{
    margin-top: 0px;
    margin-bottom: 1rem;
}
h1{
    margin: 0px;
    color: black;
    font-weight: 300;
    font-size: 2rem;

}

p {
  line-height: 1.5;

}

a, a:visited, a:hover {
  text-decoration: none;
  color: black;
  cursor: pointer;
  font-weight: 400;
  transition: 0.3s;
}




// Spacing
.container {
  max-width: 69.375rem;
  margin: 0 auto;
  padding-right: 1rem;
  padding-left: 1rem;
  @include breakpoint-up(medium){
    padding-right: 2rem;
    padding-left: 2rem;

  }

  
}
//FLexx
.flex {
  display: flex;

  &-jc-sb {
    justify-content: space-between;
  }
  
  &-jc-c {
    justify-content: center;
  }
  
  &-ai-c {
    align-items: center;
  }
}
//button



// Visibility
.hide-for-mobile {
  @include breakpoint-down(medium){
    display: none;
  }
}

.hide-for-desktop {
  @include breakpoint-up(large){
    display: none;
  }
}
//wxtra
 .box__shadow{
      -webkit-box-shadow: 5px 4px 15px 0px #000000; 
      box-shadow: 5px 4px 15px 0px #000000;
 }
.dflex{
  display: flex;
}
